
.big-image img, .slides-block img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: center;
     object-position: center;
}
.big-image {
  width: 100%;
  height: 41.6vmax;
  overflow: hidden;
}
.slides-block {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.slides-block__left-side, .slides-block__right-side {
    -ms-flex-preferred-size: 50%;
        flex-basis: 50%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.slides-block__item {
    height: 21vmax;
    -ms-flex-preferred-size: 50%;
        flex-basis: 50%;
    cursor: pointer;
    overflow: hidden;
}
.slides-block__item--wide {
      -ms-flex-preferred-size: 100%;
          flex-basis: 100%;
}
.slides-block__item:hover img {
      -webkit-transform: scale(1);
              transform: scale(1);
}
.slides-block img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition: 0.7s;
    transition: 0.7s;
    will-change: transform;
}



