
.contact-us {
  position: relative;
  width: 100%;
  height: 21vmax;
  background-color: #4b75ff;
  color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.contact-us__left-side, .contact-us__right-side {
    -ms-flex-preferred-size: 50%;
        flex-basis: 50%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.contact-us__left-side {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    text-align: left;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding-left: 13rem;
}
.contact-us__right-side {
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding-left: 60px;
}
.contact-us__left-wrap {
    position: relative;
}
.contact-us__left-wrap:hover .contact-us__arrow {
      -webkit-transform: translateX(25px) scale(1.6);
              transform: translateX(25px) scale(1.6);
}
.contact-us__desc {
    margin-bottom: 0.4rem;
}
.contact-us__arrow {
    width: 46px;
    position: absolute;
    left: -6.13rem;
    cursor: pointer;
    top: 0.6rem;
    -webkit-transition: .3s;
    transition: .3s;
}
.contact-desc {
  font-family: Gilroy;
  font-size: 0.866667rem;
  font-style: italic;
}
.contact-title {
  font-family: GilroyBold;
  text-transform: uppercase;
  font-size: 1.86667rem;
}
.contact-mail {
  text-decoration: underline;
  font-family: GilroyBold;
  font-size: 1rem;
}



